@import url('https://fonts.googleapis.com/css?family=Orbitron:400,900');

::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background: #000;
  border-left: 3px solid #000;
  border-right: 3px solid #000;
}
::-webkit-scrollbar-thumb {
  background: rgba(176,196,222,1);
  border-left: 4px solid #000;
  border-right: 4px solid #000;
}
* {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  position: relative;
}
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}

body,html {
  background: url(https://img2.cgtrader.com/items/671249/905a8ffe7a/star-field-space-background-3d-model.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 15px;
  line-height: 1.6em;
  overflow: hidden;
}
body:after,
html:after {
  background: url(https://i.ytimg.com/vi/fOl6TUPTcO8/maxresdefault.jpg);
  width: 100%;
  height: 100%;
  opacity: 0.8;
}

h1,h2,h3,h4,h5,h6,em,strong {
  font-family: 'Orbitron', monospace, sans-serif;
  letter-spacing: 1em;
  font-size: 1.5em;
}
p, blockquote{
  font-family: Arial, sans-serif;
}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}



